.venue-section {
	position: relative;
	padding: 5rem 0;
	@include max-md {
		padding: 3rem 0;
		.container {
			padding: 0;
		}
	}
	.venue-wrapper {
		position: relative;
		padding: 5rem 0 0;
		z-index: 3;
		&::before {
			position: absolute;
			top: 0;
			left: 50%;
			transform: translate(-50%, 0);
			width: 68%;
			height: 100%;
			background-color: var(--color-primary);
			opacity: 0.25;
			border-radius: var(--border-radius);
			content: "";
			@include max-md {
				width: 90%;
			}
		}
		@include max-md {
			padding: 3rem 0 0;
		}
		.venue-description {
			position: relative;
			margin: 0 auto 5rem;
			z-index: 2;
			text-align: center;
			@include max-lg {
				margin-bottom: 3rem;
			}
			@include max-md {
				padding: 0 2rem;
			}
		}
		.venue-content {
			position: relative;
			.ribbon-venue {
				position: relative;
				svg {
					width: 40px;
					height: auto;
					path {
						stroke: var(--color-dark);
					}
				}
			}
			.card {
				background-color: transparent;
				height: 100%;
				z-index: 5;
				text-align: center;
				border: none;
				.card-body {
					position: relative;
					padding: 0;
					h4 {
						margin: 0 0 1rem;
					}
					hr {
						display: block;
						width: 13%;
						height: 2px;
						margin: 0 auto 0.25rem;
					}
				}
			}
		}
	}
	.prokes-wrapper {
		margin: 3rem 0 0;
		position: relative;
		padding: 2rem;
		text-align: center;
		z-index: 2;
		.title-section {
			&::before {
				all: unset;
			}
		}
		@include max-md {
			padding: 2rem 0;
		}
		* {
			position: relative;
		}
	}
	.prokes-wrap {
		display: flex;
		gap: 0.75rem;
		justify-content: center;
		align-items: flex-start;
		flex-wrap: wrap;
		text-align: center;
		.prokes {
			display: flex;
			flex-direction: column;
			align-items: center;
			justify-content: flex-start;
			width: 132px;
			@include max-md {
				width: 120px;
			}
			@include max-xs {
				width: 45%;
			}
		}
	}
	.row-venue {
		@include max-md {
			gap: 3rem;
		}
	}
	.event-name {
		position: relative;
		background: var(--color-light);
		border-radius: 500px;
		width: fit-content;
		max-width: 100%;
		margin: 0 auto;
		padding: 5rem 2rem;
		z-index: 3;
		@include max-md {
			width: 85%;
		}
		&::before {
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
			width: calc(100% + 1rem);
			height: calc(100% + 1rem);
			border-radius: 500px;
			border: 2px solid #fff;
			content: "";
		}
		.date {
			position: relative;
			width: 100%;
			display: flex;
			flex-direction: column;
			justify-content: center;
			align-items: center;
			margin: 0.5rem 0 0.25rem;
			h5 {
				font-family: $font-secondary;
				font-weight: 700;
				margin-bottom: 0;
				font-size: 32px;
				min-width: 30%;
			}
		}
		h4 {
			font-family: $font-secondary;
		}
		p {
			margin-bottom: 0;
			font-weight: 600;
		}
		.elements-wrapper-absolute {
			overflow: hidden;
			border-radius: 500px;
			opacity: 0.6;
		}
	}
	.event-place {
		position: relative;
		background: var(--color-primary);
		border-radius: var(--border-radius);
		color: var(--color-light);
		padding: 5rem 2rem 2rem;
		margin-top: -3rem;
		@include max-md {
			padding: 5rem 1rem 2rem;
		}
		.no-event {
			padding: 3rem 2rem;
			@include max-md {
				padding: 2rem 1rem;
			}
		}
		.widget-elements {
			position: absolute;
			bottom: 0;
			left: 50%;
			transform: translate(-50%, 50%);
		}
	}
	.animation-loop {
		.st1 {
			will-change: transform;
			animation: zoomInRing 2000ms ease-out infinite;
		}
		.dot {
			will-change: transform;
			animation: fadeBoing 2000ms ease-out infinite;
			transform-origin: 50% 75%;
		}
		#Layer_3 {
			will-change: transform;
			animation: boing 1000ms ease-out infinite alternate;
		}
	}

	@keyframes zoomInRing {
		0% {
			opacity: 0;
			transform: scale3d(0.5, 0.5, 0.5);
		}
		40% {
			opacity: 1;
		}
		70% {
			opacity: 0;
			transform: scale3d(1.25, 1.25, 1.25);
		}
		100% {
			opacity: 0;
		}
	}

	@keyframes boing {
		0% {
			transform: translate3d(0, 0, 0);
		}
		100% {
			transform: translate3d(0, -5rem, 0);
		}
	}

	@keyframes fadeBoing {
		0% {
			opacity: 1;
		}
		50% {
			transform: scale3d(0.25, 0.25, 0.25);
			opacity: 0;
		}
		100% {
			transform: scale3d(1, 1, 1);
			opacity: 1;
		}
	}
}

.maps-embed {
	width: 100%;
	height: 100%;
	position: absolute;
	top: 0;
	left: 0;
}
