.reminder-wrap {
	position: relative;
	display: block;
	padding: 0 0 5rem;
	@include max-md {
		padding: 0 0 3rem;
	}
	.elements-wrapper-absolute {
		overflow: hidden;
		border-radius: 500px;
	}
	.reminder-content {
		position: relative;
		width: fit-content;
		max-width: 100%;
		margin: 0 auto;
		padding: 5rem 3rem;
		color: var(--color-dark);
		&::before {
			position: absolute;
			top: 0;
			right: 0;
			bottom: 0;
			left: 0;
			background-color: var(--color-main);
			opacity: 0.6;
			border-radius: 500px;
			content: "";
		}
		&::after {
			position: absolute;
			top: 50%;
			left: 50%;
			width: calc(100% + 1rem);
			height: calc(100% + 1rem);
			border-radius: 500px;
			border: 2px solid var(--color-main);
			transform: translate(-50%, -50%);
			content: "";
		}
	}
	.btn-reminder {
		z-index: 3;
		position: relative;
		margin: 0 auto;
		display: flex;
		align-items: center;
		position: relative;
		gap: 0.5rem;
		transition: all 0.2s ease-in-out;
		text-decoration: none;
		margin-top: 1.5rem;
		path {
			fill: var(--text-button);
			transition: all 0.3s ease-in-out;
		}
	}
}
