.couple-section {
	position: relative;
	text-align: center;
	background-color: var(--color-primary);
	color: var(--color-light);
	.and {
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		font-weight: 500;
		display: flex;
		justify-content: center;
		align-items: center;
		width: 100%;
		margin-bottom: 1rem;
		z-index: 3;
		@include max-md {
			top: 40%;
		}
		span {
			position: relative;
			font-size: 32px;
			font-family: $font-secondary;
		}
	}
	.couple-wrapper {
		position: relative;
		padding: 5rem 0;
		@include max-md {
			padding: 5rem 0 3rem;
		}
	}
	.couple-body {
		position: relative;
	}
	.couple-content {
		position: relative;
		padding: 3rem 2rem;
		margin-bottom: 18rem;
		&::before {
			position: absolute;
			top: 0;
			right: 0;
			bottom: 0;
			left: 0;
			background: var(--color-main);
			opacity: 0.6;
			border-radius: var(--border-radius);
			content: "";
		}
	}
	.couple {
		position: relative;
		text-align: center;
		padding: 0.01rem;
		&::before {
			position: absolute;
			top: 0;
			right: 0;
			bottom: 0;
			left: 0;
			background: var(--color-primary);
			opacity: 0.25;
			border-radius: var(--border-radius);
			content: "";
		}
		&.man {
			margin-bottom: 10rem;
		}
		&.woman {
			margin-top: 10rem;
		}
		.elements-wrapper-absolute {
			overflow: hidden;
			border-radius: var(--border-radius);
		}
		.image-wrap {
			width: 70%;
			position: relative;
			aspect-ratio: 3/4;
			margin: -13rem auto 0;
			.image-element {
				width: 100%;
				height: 100%;
				border-radius: 300px;
				padding: 8px;
				border: 2px solid var(--color-main);
				.couple-image {
					height: 100%;
					object-fit: cover;
					width: 100%;
					border-radius: 300px;
				}
			}
		}
		.couple-description {
			position: relative;
			padding: 2rem 1rem;
		}
		.couple-parent {
			width: 100%;
		}
		.sosmed-wrap {
			position: absolute;
			bottom: 0;
			left: 50%;
			transform: translate(-50%, 45%);
			width: fit-content;
			max-width: 100%;
			background: var(--color-secondary);
			padding: 0.5rem 0.6rem;
			border-radius: 50px;
			margin: 0 auto;
			.sosmed {
				color: var(--text-button);
				text-decoration: none;
				display: block;
				width: fit-content;
				margin: 0 auto;
				small {
					align-items: center;
					gap: 0.25rem;
					display: flex;
					justify-content: center;
				}
			}
			.fab {
				font-size: 20px;
				@include max-md {
					font-size: 18px;
				}
			}
		}
	}
}
