.story-section {
	position: relative;
	.story {
		position: relative;
		padding: 5rem 0;
		@include max-md {
			padding: 3rem 0;
		}
	}
	.story-content {
		position: relative;
	}
	.story-description {
		position: relative;
		text-align: center;
	}
	.image-wrapper {
		position: relative;
		width: 100%;
		height: auto;
		aspect-ratio: 3/4;
		margin-bottom: 3rem;
		.image-element {
			position: relative;
			width: 100%;
			height: 100%;
			border-radius: 500px;
			border: 2px solid var(--color-primary);
			padding: 8px;
			.story-background {
				position: relative;
				object-fit: cover;
				width: 100%;
				height: 100%;
				border-radius: 500px;
			}
		}
	}
}
